.Answers {
    list-style: none;
    margin: 0;
    padding: 0;
}
.Answers li{
    margin: 0 0 5px;
    padding: 10px;
    background-color: white;
    border-left: 6px solid blue;
}
 
.Answers li:hover{
    cursor: pointer;
    background-color: rgba(0, 183, 255, 0.836);
    
}
.Answers .correct,
.Answers .correct:hover{
    background-color:green;
    border-left:4px solid rgb(32, 41, 2);
    color:white;
}
.Answers .incorrect,
.Answers .incorrect:hover{
    background-color:red;
        border-left:4px solid rgb(92, 10, 10);
    color: white;
}

.Answers[disabled]{
    pointer-events: none;
    opacity: 0.7;
}