.NextStep{
    color: white;
    padding: 20px 40px;
    border: 0;
    cursor: pointer;
    background-color: rgb(27, 134, 216);
    border-radius: 4px;
}

.NextStep:hover{
    background-color: rgb(0, 51, 128);
}
.NextStep[disabled]{
    display: none;
}
.finalpage{
    text-align: center;
}
.finalpage h1{
    margin: 15px 0;
    font-size: 30px;
    font-family: Arial, Helvetica, sans-serif;
    color: rgb(30, 53, 155);
    text-transform: none;
    

}
